@import 'element';
@import 'mixin';
@import 'page';
@import 'component';



.poker {@include position-relative; @include size; overflow:hidden }
.poker .page-area { @include position-relative; @include size; overflow:hidden }
.poker .sound-area{@include screen-out; height:1px; width:1px}


.poker ::-moz-selection { background: transparent}
.poker ::-webkit-selection {background: transparent}
.poker ::-ms-selection {background: transparent}
.poker ::selection {background: transparent}
